export default {
  data () {
    return {
      elVisible: false, // 组件是否在页面上可见
      offset: 100 // 可见偏移量
    }
  },
  methods: {
    calcElVisile () {
      const elTop = this.$el.getBoundingClientRect().top
      const elHeight = this.$el.offsetHeight
      const domHeight = document.body.offsetHeight
      // top < domHeight && top > -elHeight 可见
      this.elVisible = elTop < domHeight - this.offset && elTop > this.offset - elHeight
    }
  },
  created () {
    if (window.addEventListener) {
      window.addEventListener('scroll', this.calcElVisile)
    }
  },
  mounted () {
    // 挂载完成计算一次
    this.calcElVisile()
  }
}
