<template>
  <div class="base-box" :class="{mask: mask}">
    <!-- 防止动画抖动, 定死高度 -->
    <div style="height: 42px;margin-bottom: 0.5em;">
      <transition enter-active-class="animated bounceIn">
        <h2 v-show="elVisible" class="box-title" :style="titleStyle">
          {{title}}
          <slot name="box-title"></slot>
        </h2>
      </transition>
    </div>
    <div v-if="desc" style="height: 20px;margin-bottom: 0.5em;">
      <transition enter-active-class="animated fadeInDown">
        <div v-show="elVisible" class="box-desc" :style="descStyle">
          <span style="margin: 0 1em;">
            {{desc}}
            <slot name="box-desc"></slot>
          </span>
          <router-link v-if="moreLink" :to="moreLink">查看更多</router-link>
        </div>
      </transition>
    </div>
    <div class="box-content" :style="contentStyle">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import compVisible from '@/mixins/common/compVisible'

export default {
  name: 'baseBox',
  mixins: [compVisible],
  props: {
    mask: Boolean, // 是否开启遮罩, 使用背景图时需要
    title: String, // 标题
    desc: String, // 描述
    moreLink: String // 展示更多
  },
  data () {
    return {
      titleStyle: {
        color: this.mask ? '#fff' : '#333',
        position: 'relative',
        zIndex: 2
      },
      descStyle: {
        color: this.mask ? '#fff' : '#999',
        position: 'relative',
        zIndex: 2
      },
      contentStyle: {
        color: this.mask ? '#fff' : '#333',
        position: 'relative',
        zIndex: 2
      }
    }
  }
}
</script>

<style scoped>
.base-box {
  position: relative;
  background-color: #f0f0f0;
  padding: 36px 0;
}
.box-title {
  font-size: 32px;
  line-height: 1;
  text-align: center;
}
.box-desc {
  font-size: 12px;
  line-height: 1;
  text-align: center;
}
.box-content {
  font-size: 14px;
}
.mask::after {
  content: "";
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
</style>
