<template>
  <div class="img-card">
    <div class="img-box" :style="imgBoxStyle"></div>
    <div class="content-box" @mouseover="contentVisible = true" @mouseout="contentVisible = false">
      <transition enter-active-class="animated fadeInDown" leave-active-class="animated fadeOutUp">
        <h3 v-show="contentVisible">{{title}}</h3>
      </transition>
      <transition enter-active-class="animated fadeInDown" leave-active-class="animated fadeOutUp">
        <p v-show="contentVisible">{{desc}}</p>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'imgCard',
  props: {
    img: String,
    title: String,
    desc: String
  },
  computed: {
    imgBoxStyle () {
      return {
        backgroundImage: `url(${require('@/assets/' + this.img)})`
      }
    }
  },
  data () {
    return {
      contentVisible: false
    }
  }
}
</script>

<style scoped lang="less">
.img-card {
  width: 100%;
  height: 22vw;
  position: relative;
  overflow: hidden;
  background-color: #fff;
  &::before {
    content: "";
    position: absolute;
    z-index: 1;
    width: 0;
    height: 0;
    top: 50%;
    right: 50%;
    bottom: 50%;
    left: 50%;
    background-color: rgba(0, 0, 0, 0.2);
    transform: rotate(360deg);
    transition-property: all;
    transition: 0.1s ease-out 0s;
  }

  &:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    &::before {
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform: rotate(0deg);
      background-color: rgba(0, 0, 0, 0.7);
    }
    .img-box {
      transform: scale(1.2);
    }
  }

  .img-box {
    width: 100%;
    height: 22vw;
    background-repeat: no-repeat;
    background-size: cover;
    transition: transform 1s ease-out 0s;
  }
}
.content-box {
  color: #fff;
  padding: 20px;
  font-size: 14px;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;

  h3 {
    font-size: 20px;
    color: #fff;
    margin-bottom: 0.5em;
  }
}
</style>
