<template>
  <base-box
    class="application"
    title="行业应用"
    desc="最新的行业动态"
    more-link="/help?id=2"
    style="height: 450px; overflow: hidden;"
  >
    <transition enter-active-class="animated bounceIn">
      <div class="content" v-if="elVisible">
        <el-carousel :interval="4000" type="card" height="300px" @change="carouselChange">
          <el-carousel-item v-for="(item, index) in appData" :key="item.link">
            <div class="application-box" :style="{backgroundImage: `url(${item.img})`}">
              <div class="box-content" v-if="activeIndex === index">
                <h3 class="box-title">
                  <router-link :to="item.link">{{item.title}}</router-link>
                </h3>
                <p class="box-desc">{{item.desc}}</p>
              </div>
            </div>
          </el-carousel-item>
        </el-carousel>
      </div>
    </transition>
  </base-box>
</template>

<script>
import BaseBox from './BaseBox'
import compVisible from '@/mixins/common/compVisible'

export default {
  name: 'introduce',
  mixins: [compVisible],
  components: { BaseBox },
  data () {
    const data = []
    for (let _i = 0; _i < 6; _i++) {
      data.push({
        link: `/help?id=${_i}`,
        img: 'http://i4.hexunimg.cn/2014-10-29/169812598.jpg',
        title: '维克森林大学开发出用于器官、组织和骨骼的3D打印机',
        desc: '尽管包括苹果、三星在内的手机品牌都在致力于研发新一代的可折叠智能手机。可穿戴式健康设备也在向更轻、更薄、可折叠的领域发展。但其中的电能存储却是制约可折叠的一大因素。如何生产出超能的电容器？现在LLNL的最新研究成果有望解决这一问题。'
      })
    }
    return {
      activeIndex: 0,
      appData: data
    }
  },
  methods: {
    carouselChange (index) {
      this.activeIndex = index
    }
  }
}
</script>

<style scoped>
.application {
  background-color: #e6e6e6;
}
.content {
  width: 1100px;
  margin: 0 auto;
}
.application-box {
  position: relative;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}
.box-content {
  padding: 20px 10px 0;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  bottom: 0;
}
.box-title {
  font-size: 16px;
}
.box-desc {
  font-size: 12px;
  color: #999;
}
.box-desc:hover {
  color: #fff;
}
</style>
