<template>
  <a-carousel arrows :before-change="beforeChange">
    <div slot="prevArrow" class="my-slick-arrow" style="left: 10px;z-index: 1;">
      <a-icon type="left-circle"/>
    </div>
    <div slot="nextArrow" class="my-slick-arrow" style="right: 10px">
      <a-icon type="right-circle"/>
    </div>
    <div style="position: relative;">
      <video id="bannerVideo-1" muted="muted" height="600" loop="loop">
        <source src="https://test-img.3dker.cn/ce93561db17b46288ee7b619ccb717df" type="video/mp4">
      </video>
      <div class="mask flex-middle">
        <div :class="{activeItem: activeIndex === 0}">
          <h2 class="banner-title">3D打印</h2>
          <p class="banner-desc">XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</p>
        </div>
      </div>
    </div>
    <div style="position: relative;">
      <video id="bannerVideo-2" muted="muted" height="600" loop="loop">
        <source src="https://test-img.3dker.cn/e3f98f68b119494fb7ee9f7560167de5" type="video/mp4">
      </video>
      <div class="mask flex-middle">
        <div :class="{activeItem: activeIndex === 1}">
          <h2 class="banner-title">3D打印</h2>
          <p class="banner-desc">XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</p>
        </div>
      </div>
    </div>
    <div style="position: relative;">
      <div class="banner-img img-01"></div>
      <div class="mask flex-middle">
        <div :class="{activeItem: activeIndex === 2}">
          <h2 class="banner-title">3D打印</h2>
          <p class="banner-desc">XXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX</p>
        </div>
      </div>
    </div>
  </a-carousel>
</template>

<script>
export default {
  name: 'homeBanner',
  data () {
    return {
      activeIndex: 0
    }
  },
  methods: {
    /**
     * 播放视频
     * @param {Element} videoEle : video node element
     */
    playVideo (videoEle) {
      if (videoEle.paused) {
        videoEle.play()
      }
    },
    /**
     * 暂停视频
     * @param {Element} videoEle : video node element
     */
    pauseVideo (videoEle) {
      if (!videoEle.paused) {
        videoEle.pause()
      }
    },
    /**
     * 轮播切换前回调
     */
    beforeChange (from, to) {
      this.activeIndex = to
      // 视频播放和停止
      const video1 = document.getElementById('bannerVideo-1')
      const video2 = document.getElementById('bannerVideo-2')
      switch (to) {
        case 0:
          this.playVideo(video1)
          this.pauseVideo(video2)
          break
        case 1:
          this.playVideo(video2)
          this.pauseVideo(video1)
          break
        default:
          this.pauseVideo(video1)
          this.pauseVideo(video2)
          break
      }
    }
  },
  mounted () {
    // 挂载成功播放动画
    document.getElementById('bannerVideo-1').play()
  }
}
</script>

<style scoped>
.mask {
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
}

.activeItem {
  transform: scale(1.2);
  transition: transform 2s ease-out;
}

.banner-title {
  color: #fff;
  font-size: 60px;
  font-weight: bold;
}

.banner-desc {
  color: #fff;
  font-size: 30px;
}

.ant-carousel >>> .slick-slide {
  text-align: center;
  height: 600px;
  overflow: hidden;
}

.my-slick-arrow {
  width: 30px;
  height: 30px;
  font-size: 30px;
  color: #fff;
  opacity: 0.3;
}
.my-slick-arrow:before {
  display: none;
}
.my-slick-arrow:hover {
  color: #fff;
  opacity: 0.8;
}

.banner-img {
  width: 100%;
  height: 600px;
  background-size: cover;
  background-repeat: no-repeat;
}
.banner-img.img-01 {
  background-image: url("~@/assets/images/banner/banner-img.jpg");
}
</style>
