      <span class="ellipsis" slot="description">{{desc}}</span>
    </a-card-meta>
    <template class="ant-card-actions" slot="actions">
      <router-link :to="link">立即预约</router-link>
    </template>
  </a-card>
</template>

<script>
export default {
  name: 'AppointmentItem',
  props: {
    title: {
      type: String,
      default: ''
    },
    desc: {
      type: String,
      default: ''
    },
    link: {
      type: String,
      default: ''
    },
    img: {
      type: String,
      default: ''
    }
  }
}
